___CSS_LOADER_EXPORT___.push([module.id, "\ndiv.log-view[data-v-c9d4ddf0] {\n\t\tborder-left:1px solid var(--very-quiet-text-color);\n\t\tdisplay:flex;\n\t\tflex-flow: column nowrap;\n\t\tmargin-left: var(--md-gap);\n\t\tflex-basis:20%;\n\t\tmax-width: 14em;\n\t\tmax-height: inherit;\n        min-width: 8em;\n        margin: 0;\n}\ndiv.log-view div.top-span[data-v-c9d4ddf0] {\n\t\tmax-height: unset;\n\t\tborder-bottom: 1px solid var(--separator-color);\n\t\tdisplay:flex;\n\t\tflex-flow: row nowrap;\n\t\tmargin: var(--tiny-gap) 0;\n\t\tpadding-bottom: var(--tiny-gap);\n}\ndiv.log-view div.top-span .checks[data-v-c9d4ddf0] {\n        display: grid; grid-template-columns: 1fr 1fr; grid-template-rows: 1fr 1fr;\n        height: 4em; align-items: center;\n\t\tfont-size: 0.8em;\n\t\tdisplay:flex;\n\t\tflex-flow: row wrap;\n\t\talign-items: flex-start;\n}\ndiv.log-view div.top-span .checks span[data-v-c9d4ddf0] { flex-basis: 50%; overflow: hidden; white-space: nowrap;\n}\ndiv.log-view .outlog[data-v-c9d4ddf0] {\n\t\tdisplay:flex;\n        overflow-y: auto; flex-direction: column;\n        max-height: unset; flex: 1; font-size: var(--compact-small-font);\n}\n\n", "",{"version":3,"sources":["webpack://./src/ui/outlog.vue"],"names":[],"mappings":";AA+GA;EACA,kDAAA;EACA,YAAA;EACA,wBAAA;EACA,0BAAA;EACA,cAAA;EACA,eAAA;EACA,mBAAA;QACA,cAAA;QACA,SAAA;AACA;AAEA;EACA,iBAAA;EACA,+CAAA;EACA,YAAA;EACA,qBAAA;EACA,yBAAA;EACA,+BAAA;AACA;AAEA;QACA,aAAA,EAAA,8BAAA,EAAA,2BAAA;QACA,WAAA,EAAA,mBAAA;EACA,gBAAA;EACA,YAAA;EACA,mBAAA;EACA,uBAAA;AACA;AACA,0DAAA,eAAA,EAAA,gBAAA,EAAA,mBAAA;AAAA;AAEA;EACA,YAAA;QACA,gBAAA,EAAA,sBAAA;QACA,iBAAA,EAAA,OAAA,EAAA,oCAAA;AACA","sourcesContent":["<script>\nimport { LogTypes} from '../events';\nimport Game from '../game';\n\n/**\n * Displays event output to user.\n */\nexport default {\n\n\tdata() {\n\n\t\tvar filter = 0;\n\t\tfor( let p in LogTypes) {\n\t\t\tfilter |= LogTypes[p];\n\t\t}\n\n\t\treturn {\n\t\t\tlog:Game.log,\n\t\t\tLogTypes:LogTypes,\n\t\t\tfilter:filter,\n\t\t\tshowOptions:false\n\n\t\t\t/**\n\t\t\t * @property {string[]} exclude - types to exclude.\n\t\t\t */\n\t\t}\n\n\t},\n\tmethods:{\n\n\t\tchanged( box ) {\n\n\t\t\tif ( box.checked ) {\n\t\t\t\tthis.filter = this.filter|box.value;\n\t\t\t} else {\n\t\t\t\tthis.filter = (~box.value)&this.filter;\n\n\t\t\t}\n\n\t\t},\n\n\t\tdoOptions() {\n\t\t\tthis.showOptions = !this.showOptions;\n\t\t},\n\n\t\tclearLog(){ this.log.clear(); }\n\n\t},\n\tcomputed:{\n\n\t\tvisItems(){\n\n\t\t\tlet all = this.log.items;\n\n\t\t\tlet a = [];\n\n\t\t\tfor( let i = all.length-1; i>=0; i-- ) {\n\n\t\t\t\tvar it = all[i];\n\t\t\t\tif ( !it.type || (this.filter&it.type)>0 ) a.push(it);\n\t\t\t}\n\t\t\treturn a;\n\n\t\t}\n\n\t}\n\n}\n</script>\n\n\n<template>\n\n\t<div class=\"log-view\">\n\n\t\t\t<div class=\"top-span\">\n\t\t\t\t<button type=\"button\" class=\"inline btn-sm\" @click=\"clearLog\">Clear</button>\n\t\t\t\t<!--<button class=\"text-button\" @click=\"doOptions\">&#9881;</button>-->\n\t\t\t\t<span class=\"checks\">\n\n\t\t\t\t<span v-for=\"(p,k) in LogTypes\" :key=\"k\">\n\t\t\t\t\t<input type=\"checkbox\" :value=\"p\" :id=\"elmId(k)\" :checked=\"filter&p\" @change=\"changed( $event.target )\" >\n\t\t\t\t\t<label :for=\"elmId(k)\">{{ k.toString().toTitleCase() }}</label>\n\t\t\t\t</span>\n\t\t\t\t</span>\n\n\t\t\t\t<div v-if=\"showOptions\" class=\"options\">\n\t\t\t\t</div>\n\n\t\t\t</div>\n\n\n\t\t\t<div class=\"outlog\">\n\t\t\t<div class=\"log-item\" v-for=\"(it,i) in visItems\" :key=\"i\">\n\n\t\t\t\t<div>\n\t\t\t\t\t<span v-if=\"it.title\" class=\"log-title item-name\">{{it.title}}</span>\n\t\t\t\t</div>\n\t\t\t\t<span class=\"log-text\">{{ it.text }}</span>\n\t\t\t\t<span class=\"num-align\" v-if=\"it.count>1\">(x{{it.count}})</span>\n\n\t\t\t</div>\n\t\t\t</div>\n\n\t</div>\n\n</template>\n\n<style scoped>\n\n\n    div.log-view {\n\t\tborder-left:1px solid var(--very-quiet-text-color);\n\t\tdisplay:flex;\n\t\tflex-flow: column nowrap;\n\t\tmargin-left: var(--md-gap);\n\t\tflex-basis:20%;\n\t\tmax-width: 14em;\n\t\tmax-height: inherit;\n        min-width: 8em;\n        margin: 0;\n\t}\n\n    div.log-view div.top-span {\n\t\tmax-height: unset;\n\t\tborder-bottom: 1px solid var(--separator-color);\n\t\tdisplay:flex;\n\t\tflex-flow: row nowrap;\n\t\tmargin: var(--tiny-gap) 0;\n\t\tpadding-bottom: var(--tiny-gap);\n\t}\n\n    div.log-view div.top-span .checks {\n        display: grid; grid-template-columns: 1fr 1fr; grid-template-rows: 1fr 1fr;\n        height: 4em; align-items: center;\n\t\tfont-size: 0.8em;\n\t\tdisplay:flex;\n\t\tflex-flow: row wrap;\n\t\talign-items: flex-start;\n    }\n    div.log-view div.top-span .checks span { flex-basis: 50%; overflow: hidden; white-space: nowrap; }\n\n\tdiv.log-view .outlog {\n\t\tdisplay:flex;\n        overflow-y: auto; flex-direction: column;\n        max-height: unset; flex: 1; font-size: var(--compact-small-font);\n\t}\n\n</style>\n"],"sourceRoot":""}]);
